<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                              <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="gonghao">
          <el-input v-model="ruleForm.gonghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='yuangong'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in yuangongxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="quyu">
          <el-input v-model="ruleForm.quyu" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='yuangong'"  label="" prop="bumen">
          <el-select v-model="ruleForm.bumen" placeholder="">
            <el-option
                v-for="(item,index) in yuangongbumenOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='yuangong'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="yuangongtouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="shenfenzheng">
          <el-input v-model="ruleForm.shenfenzheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="dianhua">
          <el-input v-model="ruleForm.dianhua" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='yuangong'"  label="" prop="zhanghao">
          <el-input v-model="ruleForm.zhanghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="zhanghao">
          <el-input v-model="ruleForm.zhanghao" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="mima">
          <el-input v-model="ruleForm.mima" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="xingming">
          <el-input v-model="ruleForm.xingming" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='quyuguanlirenyuan'"  label="" prop="xingbie">
          <el-select v-model="ruleForm.xingbie" placeholder="">
            <el-option
                v-for="(item,index) in quyuguanlirenyuanxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item v-if="flag=='quyuguanlirenyuan'"  label="" prop="bumen">
          <el-select v-model="ruleForm.bumen" placeholder="">
            <el-option
                v-for="(item,index) in quyuguanlirenyuanbumenOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="fuzequyu">
          <el-input v-model="ruleForm.fuzequyu" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="24">  
        <el-form-item v-if="flag=='quyuguanlirenyuan'" label="" prop="touxiang">
          <file-upload
          tip=""
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="quyuguanlirenyuantouxiangUploadChange"
          ></file-upload>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="shenfenzheng">
          <el-input v-model="ruleForm.shenfenzheng" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                        <el-col :span="12">
        <el-form-item  v-if="flag=='quyuguanlirenyuan'"  label="" prop="shouji">
          <el-input v-model="ruleForm.shouji" 
              placeholder="" clearable></el-input>
        </el-form-item>
      </el-col>
                                                                                          <el-form-item v-if="flag=='users'" label="" prop="username">
        <el-input v-model="ruleForm.username" 
        placeholder=""></el-input>
      </el-form-item>
      <el-col :span="24">
      <el-form-item>
        <el-button type="primary" @click="onUpdateHandler"> </el-button>
      </el-form-item>
      </el-col>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
                                                                  yuangongxingbieOptions: [],
                                    yuangongbumenOptions: [],
                                                                                                                                    quyuguanlirenyuanxingbieOptions: [],
                        quyuguanlirenyuanbumenOptions: [],
                                                                                                                                        };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                            this.yuangongxingbieOptions = ",".split(',')
                        this.$http({
      url: `option/bumenxinxi/bumen`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.yuangongbumenOptions = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                                                                        this.quyuguanlirenyuanxingbieOptions = ",".split(',')
                this.$http({
      url: `option/bumenxinxi/bumen`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.quyuguanlirenyuanbumenOptions = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
                                                                                          },
  methods: {
                                                                                                                                                                                                                                                                                                        yuangongtouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
                                                                                                        quyuguanlirenyuantouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
                                                                            onUpdateHandler() {
                              if((!this.ruleForm.gonghao)&& 'yuangong'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'yuangong'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                                                                                                                                                                                          if( 'yuangong' ==this.flag && this.ruleForm.shenfenzheng&&(!checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                if( 'yuangong' ==this.flag && this.ruleForm.dianhua&&(!isMobile(this.ruleForm.dianhua))){
        this.$message.error(``);
        return
      }
                                                                                                                  if((!this.ruleForm.zhanghao)&& 'quyuguanlirenyuan'==this.flag){
        this.$message.error('');
        return
      }
                                                            if((!this.ruleForm.mima)&& 'quyuguanlirenyuan'==this.flag){
        this.$message.error('');
        return
      }
                                                                                                                                                                                                                                                                                                                                          if( 'quyuguanlirenyuan' ==this.flag && this.ruleForm.shenfenzheng&&(!checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                if( 'quyuguanlirenyuan' ==this.flag && this.ruleForm.shouji&&(!isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                                                            this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
