<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangonggonghao">
        <el-input v-model="ruleForm.gonghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongmima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongquyu">
        <el-input v-model="ruleForm.quyu" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongdianhua">
        <el-input v-model="ruleForm.dianhua" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='yuangong'" label="" prop="yuangongzhanghao">
        <el-input v-model="ruleForm.zhanghao" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanzhanghao">
        <el-input v-model="ruleForm.zhanghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanmima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanfuzequyu">
        <el-input v-model="ruleForm.fuzequyu" placeholder=""></el-input>
      </el-form-item>
                                          <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanshenfenzheng">
        <el-input v-model="ruleForm.shenfenzheng" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='quyuguanlirenyuan'" label="" prop="quyuguanlirenyuanshouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                                                                                          <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                              if((!this.ruleForm.gonghao) && `yuangong` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `yuangong` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                                                                                                                                          if(`yuangong` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                if(`yuangong` == this.tableName && this.ruleForm.dianhua&&(!this.$validate.isMobile(this.ruleForm.dianhua))){
        this.$message.error(``);
        return
      }
                                                                                                                  if((!this.ruleForm.zhanghao) && `quyuguanlirenyuan` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `quyuguanlirenyuan` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                                                                                                                                          if(`quyuguanlirenyuan` == this.tableName && this.ruleForm.shenfenzheng&&(!this.$validate.checkIdCard(this.ruleForm.shenfenzheng))){
        this.$message.error(``);
        return
      }
                                                if(`quyuguanlirenyuan` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                                                            this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
